function submitSolution(id) {

    web3.eth.getAccounts(async function (err, acc) {
        if (err != null) {
            alert("There was an error fetching your accounts");
            return;
        }
        if (acc.length > 0) {
            var x = document.getElementById("account").value;
            console.log(web3.currentProvider);
            console.log(acc);
            web3.eth.defaultAccount = acc[x];
            account = acc[x];

            const SenseChainTaskDetailedContract = new web3.eth.Contract(
                ContractABI3, id
            )
            console.log("whatever this is " + id);
            console.log(account + " " + document.getElementById("data" + id).value);

            var x = document.getElementById("data" + id).value;

            const res = SenseChainTaskDetailedContract.methods.successfulreservation(account).call({ from: account, gas: 1500000 })
                .then(async function (res) {
                    console.log(res);
                    if (res != 0) {
                        const gas1 = await SenseChainTaskDetailedContract.methods.submitSolution(account, document.getElementById("data" + id).value).estimateGas();
                        console.log("Submit sol gas: " + gas1);
                        const res1 = await SenseChainTaskDetailedContract.methods.submitSolution(account, document.getElementById("data" + id).value).send({ from: account, gas: 1500000 })
                            .then(function (res1) {
                                if (res1) {
                                    //alert(err);
                                    console.log("status = " + res1);
                                    alert("Submittion successful for account " + account);
                                }
                                else {
                                    console.log("error");
                                    alert("rejected");
                                }
                            })
                    }
                    else
                        alert("You are not able to submit the task since account " + account + " has not been selected for this task");

                    window.location.assign("https://suhailo0o.github.io/");
                });
        }
    })


}



